<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div class="post-title-section rounded shadow">
<div class="row">
	<div class="col-md-8 col-8">
		<h5>{{post_title}}</h5>
	</div>
	<div class="col-md-4 col-4" style="text-align:right;">
	<router-link :to="{name : 'edit-post',params : {post_id : post_id} }" class="btn"> <i class="bi bi-pencil-square icon"></i> </router-link>
	<button class="btn" @click="delete_post()"> <i class="bi bi-trash icon"></i> </button>	
	</div>
</div>
</div>
<section style="padding-top:0px;">
<div  style="height: 300px; overflow: scroll; padding-top: 20px; padding-bottom: 0px;">
		<div v-for="message in messages" style="margin-bottom: 10px;" v-if="messages.length>0">
	<label class="post-comment">
		{{message.message}}
	</label>
	<div>
		
	</div>

	</div>
	<p style="font-size:20px;" v-if="messages.length==0">No comments yet</p>
</div>




</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				details : [],
				post_id : '',
				post_title : '',
				socket : io(this.$store.state.socketio_server),
				messages : [],	
				post_owner : ''
			}
		},
		methods : {


			 async post_details(){
			const res = await axios.get(this.$store.state.api_url
				+'post-details/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.details = res
				res.forEach((data)=>{
					this.post_title = data.postTitle 
					this.post_owner = data.userId
				})
				this.post_comments()
		},

		async post_comments(){
			const res = await axios.get(this.$store.state.api_url
				+'post-comments/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.messages = res
		},

			async delete_post(){
			const res = await axios.post(this.$store.state.api_url+'delete-post',{
				post_id : this.post_id
			}).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			
			this.$router.push('/my-posts')
			
		},
		
		},

		created(){
		this.post_id = this.$route.params.post_id	
		this.post_details()

		},


	}


</script>