import { createRouter, createWebHistory } from 'vue-router'
import Home from '../components/Home'
import MyKlub from '../components/MyKlub'
import MyPosts from '../components/MyPosts'
import Posts from '../components/Posts'
import TopicHistory from '../components/TopicHistory'
import SaveForLater from '../components/SaveForLater'
import SavedTopics from '../components/SavedTopics'
import Login from '../components/Login'
import EditPost from '../components/EditPost'
import ViewPost from '../components/ViewPost'
import WaitingRoom from '../components/WaitingRoom'
import JoinChat from '../components/JoinChat'
import ChatRoom from '../components/ChatRoom'
import AddFriend from '../components/AddFriend'
import KlubWaitingRoom from '../components/KlubWaitingRoom'
import JoinKlubChat from '../components/JoinKlubChat'
import Settings from '../components/Settings'
import KlubChatRoom from '../components/KlubChatRoom'
import PostDetails from '../components/PostDetails'
import Notifications from '../components/Notifications'
import PostCommentNotifications from '../components/PostCommentNotifications'
import ViewPostComment from '../components/ViewPostComment'
import Register from '../components/Register'
import ForgotPassword from '../components/ForgotPassword'
import NewPassword from '../components/NewPassword'


const routes = [
  {
    path: '/home',
    name: 'home',
    component: Home
  },

    {
    path: '/my-klub',
    name: 'my-klub',
    component: MyKlub
  },

   {
    path: '/my-posts',
    name: 'my-posts',
    component: MyPosts
  },

   {
    path: '/posts',
    name: 'posts',
    component: Posts
  },

     {
    path: '/topic-history',
    name: 'topic-history',
    component: TopicHistory
  },
     {
    path: '/saved-topics',
    name: 'saved-topics',
    component: SavedTopics
  },

     {
    path: '/save-for-later',
    name: 'save-for-later',
    component: SaveForLater
  },

    {
    path: '/',
    name: 'login',
    component: Login
  },

      {
    path: '/edit-post/:post_id',
    name: 'edit-post',
    component: EditPost
  },

        {
    path: '/view-post/:post_id',
    name: 'view-post',
    component: ViewPost
  },

         {
    path: '/post-details/:post_id',
    name: 'post-details',
    component: PostDetails
  },

          {
    path: '/waiting-room',
    name: 'waiting-room',
    component: WaitingRoom
  },

            {
    path: '/join-chat',
    name: 'join-chat',
    component: JoinChat
  },

              {
    path: '/join-klub-chat',
    name: 'join-klub-chat',
    component: JoinKlubChat
  },

              {
    path: '/chat-room',
    name: 'chat-room',
    component: ChatRoom
  },


                {
    path: '/add-friend',
    name: 'add-friend',
    component: AddFriend
  },

                  {
    path: '/klub-waiting-room',
    name: 'klub-waiting-room',
    component: KlubWaitingRoom
  },

    {
    path: '/settings',
    name: 'settings',
    component: Settings
  },

      {
    path: '/klub-chat-room',
    name: 'klub-chat-room',
    component: KlubChatRoom
  },


      {
    path: '/notifications',
    name: 'notifications',
    component: Notifications
  },

      {
    path: '/post-comment-notifications',
    name: 'post-comment-notifications',
    component: PostCommentNotifications
  },

    {
    path: '/view-post-comment/:id',
    name: 'view-post-comment',
    component: ViewPostComment
  },

      {
    path: '/register',
    name: 'register',
    component: Register
  },

      {
    path: '/forgot-password',
    name: 'forgot-password',
    component: ForgotPassword
  },

      {
    path: '/new-password/:email',
    name: 'new-password',
    component: NewPassword
  },
 
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
