<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
	<small style="font-size: 16px;">Save topics</small>
</div>



<!-- Trending topics -->
<section>
<div class="container">

<div class="row">
<div class="col-md-0"></div>
<div class="col-md-12" style="padding-top: 10px; text-align: center;">
	<p v-if="topics.length==0">No topics available</p>
<div v-for="topic in topics">
	<button class="btn my-post shadow" @click="create_chat(topic.topic_name)">
		<div class="row">
		<div class="col-md-10 col-10 text-overflow">
		{{topic.topic_name}} 	
		</div>
		<div class="col-md-2 col-2" style="text-align:right;">
			<i class="bi bi-chat icon"></i>
		</div>
	</div>
</button>

<div class="row">
		<div class="col-md-6 col-6" style="text-align:left;">
		</div>
		<div class="col-md-6 col-6" style="text-align:right;">
			<button class="btn" @click="delete_topic(topic.id)"> <i class="bi bi-x icon"></i> </button>
		</div>
		
	</div>

</div>

</div>
<div class="col-md-0"></div>
</div>

</div>
</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return {
				topics : [],
				topic : ''
			}
		},

			methods : {
		async saved_topics(){
			const res = await axios.get(this.$store.state.api_url+'saved-topics/'+this.$store.state.user_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.topics = res
		},

		async delete_topic(id){
			const res = await axios.post(this.$store.state.api_url+'delete-saved-topic',{
				id : id
			}).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.saved_topics()
		},

		create_chat(topic){
				this.topic = topic
		if(!this.topic){
		toast.error("Enter topic to create a chat", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });
      return
		}

		this.$store.state.chat_topic = this.topic
		localStorage.setItem('chat_topic',this.topic)
		let current_time = Date.now().toString()
		let random_number = Math.floor(Math.random() * 1000).toString()
		let current_topic_hash = current_time+random_number
		this.$store.state.current_topic_hash = current_topic_hash
		localStorage.setItem('current_topic_hash',current_topic_hash)
		this.create_topic(this.topic,current_topic_hash)
		this.$router.push('/waiting-room')

			},

			async create_topic(topic,topic_hash){
				const res = await axios.post(this.$store.state.api_url+'create-topic',{
					topic_name : topic,
					topic_hash : topic_hash,
					user_id : this.$store.state.user_id,
					klub : 0 
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
			
			}
	},

	created(){
			this.saved_topics()
		}

}
</script>