<template>
		<section style="padding:2px;">
		<div class="row shadow rounded">
	<div class="col-md-4 col-4" style="padding-top:5px;">
	<router-link to="/home" class="logo"><h3 style="color:#E46000;">Coil Chat</h3></router-link>
	</div>
	<div class="col-md-8 col-8" style="text-align: right;">
		
		<button class="btn" @click="check_note()">
			<i class="bi bi-people-fill icon"></i> <sup  :class="{ 'text-danger' : !this.$store.state.new_friend_alert, 'text-success' : this.$store.state.new_friend_alert } ">{{this.$store.state.total_friends}}</sup>
		</button>

		<router-link to="/post-comment-notifications" class="btn"> <i class="bi bi-bell-fill icon"></i> <sup style="font-size:10px;" v-if="this.$store.state.show_post_comment_alert==true || this.$store.state.show_comment_on_comment_alert==true "> <i class="bi bi-check-circle-fill text-success"></i> </sup> </router-link>

		<router-link to="/notifications" class="btn"> <i class="bi bi-heart-fill icon"></i> <sup style="font-size:10px;" v-if="this.$store.state.show_post_notes"> <i class="bi bi-check-circle-fill text-success"></i> </sup> </router-link>

	

		<button class="dropdown btn">
  <button class="btn dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
    <i class="bi bi-person icon"></i>
  </button>
  <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
    <li><a class="dropdown-item disabled">Hi,{{this.$store.state.username}}</a></li>
    
    <li><router-link to="/settings" class="dropdown-item"> <i class="bi bi-gear icon"></i> Settings</router-link></li>

    <li><button class="dropdown-item" @click="logout()"> <i class="bi bi-power icon"></i> Log Out</button></li>

    
  </ul>
</button>


	</div>
</div>
	</section>

	<div style="position:fixed; width: 100%; right: 0; top: 0; z-index: 900; margin-top: 30px; height: 100%;" v-if="this.$store.state.show_post_likes_modal">
		
		<div class="container">
			<div class="row">
				<div class="col-md-4"></div>
				<div class="col-md-4">
					
					<div style="background-color:#fff; padding: 10px; border-radius: 10px;" class="shadow-lg">

						<div class="row">
							<div class="col-md-6 col-8">
								<h6 class="icon">Notifications</h6>
							</div>
							<div class="col-md-6 col-4" style="text-align:right;">
								<button @click="this.$store.state.show_post_likes_modal=false" class="btn text-danger"> <i class="bi bi-x"></i> </button>
							</div>
						</div>
						
						<router-link :to="{ 'name' : 'post-details', params : { 'post_id' : like.post_id } }" v-for="like in post_likes" class="notification btn">
							 <i class="bi bi-check-circle icon"></i> {{like.post_title}} was <span class="icon">liked</span> 
						</router-link>

					</div>

				</div>
			</div>
		</div>

	</div>


</template>

<script>
	import axios from 'axios'
	import { io } from "socket.io-client"
	export default{
		name : 'top bar',

		data(){
			return {
				socket : io(this.$store.state.socketio_server),

			}
		},

		methods : {

			check_note(){
				this.user_details()
				this.$store.state.new_friend_alert = false
			},

			async user_details(){
				const res = await axios.get(this.$store.state.api_url+'user-details/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				res.forEach((data)=>{
						localStorage.setItem('total_friends',data.total_friends)
						this.$store.state.total_friends = data.total_friends
				})
			},

			logout(){
				this.$store.state.logged_in = false
				localStorage.clear()
				this.$router.push('/')
			}
		},

		mounted(){

		
					this.socket.emit('notification room',{'user_id': this.$store.state.user_id })
					//join to get alert when your post is liked
					this.socket.emit('like post',{ 'user_id' : this.$store.state.user_id, 'post_id' : '', 'post_owner' : '' })
					//join to get alert when someone comments on your post
					this.socket.emit('post msg',{ 'user_id' : this.$store.state.user_id,'message' : '','room':'comments alert room', 'post_owner' : '' })




			//check if my post was liked 
			this.socket.on('post for liked',(data)=>{

				if(data.post_owner==this.$store.state.user_id){
					this.$store.state.show_post_notes = true
				}
			
			})

			//get my post comments from my friends
			this.socket.on('new post comment',(data)=>{
				
				if(this.$store.state.user_id == data.post_owner ){
					this.$store.state.show_post_comment_alert = true 
				}
			
			})


			//check if someone replied on my comment
		this.socket.on('reply received',(data)=>{
		
		if(data.comment_owner == this.$store.state.user_id){
			this.$store.state.show_comment_on_comment_alert  = true
		}

		})


		

				//check if friend accepted your friend request
			this.socket.on('new notification',(data)=>{
				//this.user_details()
				if(data.friend==this.$store.state.user_id){
					this.$store.state.new_friend_alert = true
				}

				if(data.user_id==this.$store.state.user_id){
					this.$store.state.new_friend_alert = true
				}

				
			})

		}
	}
</script>


<style>
	.noti{
		position: absolute;
		margin-bottom: 100px;
	}
</style>

