<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
	<small style="font-size: 16px;">Posts</small>
</div>


<section style="padding-top:10px;">
	<div>
		<router-link :to="{name : 'view-post', params : {post_id : post.post_id} }" class="btn post shadow" v-for="post in posts"> {{post.post_title[0]}} </router-link>
		<p v-if="posts.length==0" class="text-danger"> <i class="bi bi-info-circle"></i> No posts available</p>
	</div>
</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				posts : [],
				counter:0
			}
		},
		methods : {
			async get_posts(){
				const res = await axios.get(this.$store.state.api_url+'get-posts/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.posts = res 
			}
		},
		created(){
			this.get_posts()
		}
	}
</script>