<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
<div class="row">
	<div class="col-md-10">
		<h5> <router-link to="/home" class="btn"> <i class="bi bi-chevron-left icon"></i> Notifications </router-link></h5>
	</div>
	
</div>
</div>
<section style="padding-top:0px;">



<div v-for="reply in replies">
	
	<router-link :to="{ 'name' : 'view-post', params : { 'post_id' : reply.post_id } }" class="btn response shadow" style="text-align: left;">

	<div class="row">
		<div class="col-md-1 col-1"> <h3><i class="bi bi-check-circle icon"></i></h3> </div>
		<div class="col-md-11 col-11">  Someone replied to your comment: @<strong><i>{{reply.comment}}</i></strong> on the post <strong>{{reply.post_title}}</strong> : "{{reply.message}}"    </div>
	</div>

	</router-link>

</div>


<div>
	
	<router-link :to="{ 'name' : 'view-post-comment', params : { 'id' : comment.id } }" v-for="comment in comments" class="notification btn shadow">

<div class="row">
	<div class="col-md-1 col-1"> <h3> <i class="bi bi-check-circle icon"></i> </h3>  </div>

	<div class="col-md-11 col-11" style="  white-space: nowrap; overflow: hidden;text-overflow: ellipsis;">
	 Someone commented on <strong>{{comment.post_title}}</strong>	
	</div>

</div>
</router-link>

<p class="text-danger" v-if="comments.length==0 && replies.length==0  "> <i class="bi bi-info-circle"></i> No notifications </p>



</div>




</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				socket : io(this.$store.state.socketio_server),
				comments : [],
				replies : []
			}
		},
		methods : {
		
		async my_post_comments(){
				const res = await axios.get(this.$store.state.api_url+'my-post-comments/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.comments = res
			},

			async replies_on_my_comments(){
				const res = await axios.get(this.$store.state.api_url+'replies-on-my-comments/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.replies = res
			},
		
		},

		created(){
		this.my_post_comments()
		this.replies_on_my_comments()
		this.$store.state.show_post_comment_alert = false
		this.$store.state.show_comment_on_comment_alert = false
		},


	}


</script>