<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
	<small style="font-size: 16px;">Edit post</small>
</div>

<!-- Whats on your mind -->
<form @submit.prevent="edit_post()">
<div class="form-floating">
<textarea class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style="height: 100px" v-model="post_title"></textarea>
<label for="floatingTextarea2">What's on your mind?</label>
</div>
<div style="padding-top:10px;" class="row">
	<div class="col-md-6 col-6">
		<button type="submit" class="btn btn-1"> <i class="bi bi-plus"></i>Save changes</button>
	</div>
	
</div>
</form>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return{
				post_title : '',
				details : [],
				post_id : ''
			}
		},

		methods : {
			async edit_post(){
				const res = await axios.post(this.$store.state.api_url+'edit-post',{
					post_title : this.post_title,
					post_id : this.post_id
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				
				
		toast.success("Changes saved successfully", {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      });
			},

		async post_details(){
			const res = await axios.get(this.$store.state.api_url
				+'post-details/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.details = res
				res.forEach((data)=>{
					this.post_title = data.postTitle
				})
		}

		},

		created(){
			this.post_id = this.$route.params.post_id
			this.post_details()

		}
	}
</script>