<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div style="padding-bottom: 10px; text-align:left;">
	<button class="btn btn-2 btn-sm shadow-lg" @click="delete_friend()"> <i class="bi bi-x"></i> Remove friend from klub</button>
</div>

<div class="post-title-section rounded shadow">
<div class="row">
	<div class="col-md-6 col-6">
		<h5>{{this.$store.state.chat_topic}}</h5>
	</div>
	<div class="col-md-6 col-6" style="text-align:right;">
		{{minutes}} : {{seconds}}
		<button class="btn btn-danger btn-sm" @click="leave_chat_room()"> Leave</button>	
		
	</div>
</div>
</div>
<section style="padding-top:0px;">
<div id="box" style="height: 300px; overflow: scroll; padding-top: 20px; padding-bottom: 0px;">

	<div v-for="message in messages">
		<label class="post-comment mt-3">
		{{message.message}}
	</label>
	</div>
	
</div>
	
	
	
	<div style="padding-top:30px;">
<form @submit.prevent="chat_message()">
	<div class="input-group mb-1">
  <input @input="get_words()" v-model="message" type="text" class="form-control input-design" placeholder="Leave a comment...">
  <button class="btn btn-1" type="submit" :disabled="characters.length>=character_limit" ><i class="bi bi-arrow-right-circle"></i> </button>
</div>
</form>
	</div>

		<div style="padding-top:0px;">
<div class="progress" style="height: 3px;">
  <div class="progress-bar bg-warning" role="progressbar" :style=" { width :  (characters.length/character_limit)*100+'%' } " :aria-valuenow="characters.length" aria-valuemin="0" :aria-valuemax="100"></div>
</div>
</div>



</section>

<BottomMenu />

<Footer />







</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				socket : io(this.$store.state.socketio_server),
				messages : [],
				message : '',
				minutes : 1,
				seconds : '00',
				distance_period : (1*60*1000),
				polling : '',
				show_modal : false,
				room_members : [],
				characters : '',
				character_limit : 300
			}
		},
		methods : {

			get_words(){
				this.characters = this.message
			},

			chat_message(){
				if(!this.message){
					return
				}
				this.socket.emit('chat message',{'message':this.message, 'user_id':this.$store.state.user_id, 'room' : this.$store.state.current_topic_hash })
				this.message = null
				this.get_chat_messages()
			},

			async scroll_top(){
  		var container = this.$el.querySelector("#box");
		container.scrollTop = container.scrollHeight;
		},
		
		leave_chat_room(){
			clearInterval(this.polling)
			this.socket.emit('leave chat room',{'room':this.$store.state.current_topic_hash})
			this.$router.push('/my-klub')
		},

		timer(){
  			this.polling = setInterval(()=>{
  			this.minutes = Math.floor( localStorage.getItem('distance_period') % (1000 * 60 * 60) / (1000 * 60) ) 
  			this.seconds = Math.floor( localStorage.getItem('distance_period') % (1000 * 60) / 1000 ) 
  			

  			localStorage.setItem('distance_period', localStorage.getItem('distance_period')-1000)

  			if( localStorage.getItem('distance_period')<1 ){
  				localStorage.setItem('distance_period',(30*60*1000))
  				clearInterval(this.polling)
  				toast.error("Chatting time is over", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })
  		//redirect to add friend page
  				setTimeout(()=>{
  					clearInterval(this.polling)
  					
  					if(this.messages.length>0){
  						this.$router.push('/my-klub')
  					}else{

  					toast.error("We noticed you did not chat with your friend.You  can always chat next time", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })
  	
  	setTimeout(()=>{
  		this.$router.push('/my-klub')
  	}, 3000);
	
  					}

  				}, 2000);


  			}
  			
  			},1000)
  			return
			},

		

			async delete_friend(){
				const res = await axios.post(this.$store.state.api_url+'delete-friend',{
					user_id : this.$store.state.user_id,
					friend : this.$store.state.friend
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				toast.success("Friend removed from your klub", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })
				this.leave_chat_room()
			},


			async get_chat_messages(){
				const res = await axios.get(this.$store.state.api_url+'get-chat-messages/'+this.$store.state.current_topic_hash).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				this.messages = res 

			}

		},
		
		created(){

			if(localStorage.getItem('distance_period')==''){
				localStorage.setItem('distance_period',(1*60*1000))
			}

			this.get_chat_messages()
			this.timer()


			setTimeout(()=>{
				this.show_modal = true
			},30000)


			this.socket.on('connect',()=>{
				this.socket.send('a user joined chatroom')
				//join the room,the room is the topic hash 
				this.socket.emit('join chat room',{ 'room' : this.$store.state.current_topic_hash, 'user_id'  : this.$store.state.user_id })
			})

			//check if a new message has come
			this.socket.on('new message',(data)=>{
				this.get_chat_messages()
				//this.messages = this.messages.concat(data.message)
				if(this.$store.state.user_id!=data.sender){
					this.$store.state.friend = data.sender
					localStorage.setItem('friend',data.sender)
				}
			})

			//check if someones left chat room
			this.socket.on('someone left chat room',(data)=>{
					toast.error("The person you were chatting with left", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })
	setTimeout(()=>{
		this.$router.push('/my-klub')
	}, 3000);

			})


	//check if person joined chat room and show his id
	this.socket.on('member joined',(data)=>{
		if(data!=this.$store.state.user_id){
			//console.log('Your friend is '+data)
			this.$store.state.friend = data 
			this.socket.emit('get friend',{ 'room' : this.$store.state.current_topic_hash, 'user_id'  : this.$store.state.user_id })

		}

	})



	//get friend
	this.socket.on('friend is',(data)=>{
		if(data!=this.$store.state.user_id){
			this.$store.state.friend = data
			//console.log('Your friend 2 is '+data)
		}

	})





		},

		updated(){
			this.scroll_top()
		}

		
	}


</script>

