<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />



<!-- Whats on your mind -->
<form @submit.prevent="create_chat()">
<div class="form-floating">
<textarea @input="get_words()" class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style="height: 100px" v-model="topic"></textarea>
<label for="floatingTextarea2">What's on your mind?</label>
</div>
<div style="padding-top:2px;">
<div class="progress" style="height: 3px;">
  <div class="progress-bar bg-warning" role="progressbar" :style=" { width :  (characters.length/character_limit)*100+'%' } " :aria-valuenow="characters.length" aria-valuemin="0" :aria-valuemax="100"></div>
</div>
</div>


<p v-if="detected_bad_words.length>0" class="text-danger"> Bad words detected! :  {{detected_bad_words}} </p>


<div style="padding-top:5px;" class="row">
	<div class="col-md-6 col-6">
		<button class="btn btn-1" :disabled="characters.length>=character_limit || detected_bad_words.length>0 "> <i class="bi bi-plus"></i>Create chat</button>
	</div>
	<div class="col-md-6 col-6" style="text-align: right;">
		<router-link to="/join-chat" class="btn btn-2"> <i class="bi bi-arrow-repeat"></i> Join chat</router-link>
	</div>
</div>
</form>






<!-- Trending topics -->
<section>
<div class="container">

<div class="row">
<div class="col-md-0"></div>
<div class="col-md-12" style="padding-top: 50px; text-align: center;">
<h5>Trending topics</h5>

<TrendingTopics @receive-topic="receive_topic" :create_chat_handler="create_chat" />

</div>
<div class="col-md-0"></div>
</div>

</div>
</section>


<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import TrendingTopics from './TrendingTopics'
	import { toast } from 'vue3-toastify'
	import 'vue3-toastify/dist/index.css'
	import axios from 'axios'
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu,TrendingTopics},
		data(){
			return {
				topic : '',
				characters : 0,
				character_limit : 300,
				bad_words : ['fuck','kill','bullshit'],
				detected_bad_words : []
			}
		},
		methods : {

			get_words(){
				this.characters = this.topic
				
				this.detected_bad_words = []
				this.bad_words.forEach((data)=>{
					if(this.topic.search(data)>=0){
						this.detected_bad_words.push(data)
					}
				})

			},

			receive_topic(topic_1){
				this.topic = topic_1
				
			},

			create_chat(){
		if(!this.topic){
		toast.error("Enter topic to create a chat", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });
      return
		}

		this.$store.state.chat_topic = this.topic
		localStorage.setItem('chat_topic',this.topic)
		let current_time = Date.now().toString()
		let random_number = Math.floor(Math.random() * 1000).toString()
		let current_topic_hash = current_time+random_number
		this.$store.state.current_topic_hash = current_topic_hash
		localStorage.setItem('current_topic_hash',current_topic_hash)
		this.create_topic(this.topic,current_topic_hash)
		this.$router.push('/waiting-room')

			},

			async create_topic(topic,topic_hash){
				const res = await axios.post(this.$store.state.api_url+'create-topic',{
					topic_name : topic,
					topic_hash : topic_hash,
					user_id : this.$store.state.user_id,
					klub : 0 
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
			
			}

		},

		created(){
			//set the chatting time to null
			localStorage.setItem('distance_period','')
		}

	}
</script>