<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
<div class="row">
	<div class="col-md-10">
		<h5> <router-link to="/home" class="btn"> <i class="bi bi-chevron-left icon"></i> Notifications </router-link></h5>
	</div>
	
</div>
</div>
<section style="padding-top:0px;">


<div>
	
	<router-link :to="{ 'name' : 'post-details', params : { 'post_id' : like.post_id } }" v-for="like in post_likes" class="notification btn shadow">

	<div class="row">
		<div class="col-md-1 col-1"> <h3> <i class="bi bi-check-circle icon"></i> </h3> </div>

		<div class="col-md-9 col-9" style="  white-space: nowrap; overflow: hidden;text-overflow: ellipsis;">
			{{like.post_title}}
		</div>

		<div class="col-md-2 col-2">
			<span class="icon">was liked</span>
		</div>

	</div>

					 
</router-link>

<p class="text-danger" v-if="post_likes.length==0"> <i class="bi bi-info-circle"></i> No notifications </p>

</div>



</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				socket : io(this.$store.state.socketio_server),
				post_likes : []
			}
		},
		methods : {
		
		async my_post_likes(){
				const res = await axios.get(this.$store.state.api_url+'my-post-likes/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.post_likes = res
			},
		
		},

		created(){
		this.my_post_likes()
		this.$store.state.show_post_notes = false
		},


	}


</script>