<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />








<!-- Trending topics -->
<section>
<div class="container">

<div class="row">
<div class="col-md-0"></div>
<div class="col-md-12" style="padding-top: 50px; text-align: center;">
<h5>Join chat</h5>



<div>
	
<div v-for="topic in topics">

	<div>
	<button  @click="pick_topic(topic.topicName,topic.topicHash)" class="btn trending-topic shadow">{{topic.topicName}}</button>
	</div>



</div>

</div>


</div>
<div class="col-md-0"></div>
</div>

</div>
</section>


<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import { toast } from 'vue3-toastify'
	import 'vue3-toastify/dist/index.css'
	import axios from 'axios'
	import { io } from "socket.io-client"
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return {
				topics : [],
				socket : io(this.$store.state.socketio_server,{secure : true}),
				topic_hash : '',
				friends : []
			}
		},
		methods : {

			async get_my_friends(){
				const res = await axios.get(this.$store.state.api_url+'get-my-friends/'+this.$store.state.user_id).then(function(response){
					return response.data 
				}).catch(function(error){
					console.log(error)
				})

				this.friends = res
			},


			pick_topic(topic,topic_hash){
				this.topic_hash = topic_hash
				this.socket.emit('click topic',{'clicked_topic_hash': topic_hash,'room':'topics page'})
				this.$store.state.current_topic_hash = topic_hash
				localStorage.setItem('current_topic_hash',topic_hash)
				this.$store.state.chat_topic = topic
				localStorage.setItem('chat_topic',topic)
				this.$router.push('/chat-room')
			}

		},
		created(){
		this.get_my_friends()	
		},

			mounted(){
				this.socket.on('connect',()=>{
					this.socket.emit('fetch chat topics',{'room': 'topics page'})

				})

				this.socket.on('topics',(data)=>{
					

					data.forEach((data1)=>{

						this.friends.forEach((data2)=>{
							if(data1.userId==data2.friend){

								data.pop(data1)
							}
						})


					})
					this.topics = data
					
				})

				this.socket.on('topics after clicking',(data)=>{
					
					data.forEach((data1)=>{

						this.friends.forEach((data2)=>{
							if(data1.userId==data2.friend){
								
								data.pop(data1)
							}
						})


					})
					this.topics = data
					
				})


			}
	}
</script>