<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
	<small style="font-size: 16px;">Save for later</small>
</div>



<!-- Trending topics -->
<section>
<div class="container">

<div class="row">
<div class="col-md-0"></div>
<div class="col-md-12" style="padding-top: 10px; text-align: center;">
<div>
	<button class="btn my-post shadow" v-for="topic in topics" @click="save_for_later(topic.topicHash)">
	<div class="row">
		<div class="col-md-10 col-10 text-overflow">
		{{topic.topicName}} 	
		</div>
		<div class="col-md-2 col-2" style="text-align:right;">
			<i class="bi bi-plus icon"></i>
		</div>
	</div>
	</button>
</div>

</div>
<div class="col-md-0"></div>
</div>

</div>
</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return {
				topics : []
			}
		},
			methods : {
		async trending_topics(){
			const res = await axios.get(this.$store.state.api_url+'trending-topics').then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.topics = res
		},

		async save_for_later(topic_hash){
			const res = await axios.post(this.$store.state.api_url+'save-for-later',{
				topic_hash : topic_hash,
				user_id : this.$store.state.user_id
			}).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			toast.success(res, {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      });
		},

		

	},
	created(){
		this.trending_topics()
	}
	}
</script>