<template>



<section style="padding-top: 50px;">
<div class="container">

<div class="row">
	<div class="col-md-2"></div>
<div class="col-md-4" style="padding-top: 30px;">
	<img src="/assets/images/3156814-removebg-preview.png" style="width: 100%;">
</div>
<div class="col-md-4">

<div style="padding-top:20px;">
<h3 class="logo">Coil Chat</h3>
</div>



<div style="background-color: none; padding:10px;">

	<div>
<h3>Register</h3>
</div>
	<form @submit.prevent="register()">

		<div class="row">
			<div class="col-md-6">
						<div class="form-floating mb-3">
  <input type="email" class="form-control" id="floatingInput" placeholder="Email" v-model="email">
  <label for="floatingInput">Email</label>
</div>
			</div>


		<div class="col-md-6">
					<div class="form-floating mb-3">
  <input type="text" class="form-control" id="floatingInput" placeholder="Phone" v-model="phone">
  <label for="floatingInput">Phone</label>
</div>
		</div>

		<div class="col-md-12">
			

<div class="form-floating">
  <input type="password" class="form-control" id="floatingPassword" placeholder="Password" v-model="password">
  <label for="floatingPassword">Password</label>
</div>

		</div>



		</div>



<div style="padding-top:10px;">
	<button class="btn btn-2 btn-lg" type="submit">Register</button>
</div>
	</form>

</div>

<router-link to="/" class="btn text-dark btn-lg">Already have an account</router-link>


<div style="color: grey;">
	<p>Coil Chat Inc.</p>
</div>

</div>
<div class="col-md-2"></div>
</div>

</div>
</section>



<!-- otp modal -->
<div style="position: fixed; width: 100%; top: 0; padding-top: 200px; z-index: 900;" v-if="show_modal">
	
	<div class="container">
		<div class="row">
			<div class="col-md-4"></div>
			<div class="col-md-4">
				<div class="shadow-lg rounded p-3" style="background-color:#fff;">

					<div class="row">
						<div class="col-md-9 col-9">
								<p>Enter OTP send to your email</p>
						</div>
						<div class="col-md-3 col-3">
							<div style="text-align:right;">
						<button class="btn text-danger"> <i class="bi bi-x"></i> </button>
					</div>
						</div>
					</div>

					
					
					<input type="text" v-model="otp" class="form-control" placeholder="OTP">
					<button class="btn btn-2 mt-3" @click="confirm_otp()">Send</button>
				</div>
			</div>
			<div class="col-md-4"></div>
		</div>
	</div>

</div>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return{
				email : '',
				password : '',
				phone : [],
				show_modal  : false,
				otp : ''
			}
		},
		methods : {

			async confirm_otp(){

				if(!this.otp){
		toast.error("OTP is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}
		
				const res = await axios.post(this.$store.state.api_url+'confirm-otp',{
					email : this.email,
					otp : this.otp
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				if(res=='success'){
					this.$router.push('/')
				}else{

					toast.error("Invalid OTP!", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });

				}
				
			},


			async send_otp(){
		
				const res = await axios.post(this.$store.state.api_url+'send-otp',{
					email : this.email
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				
			},


			async register(){
				if(!this.email){
		toast.error("Email is required", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}


				if(!this.phone){
		toast.error("Phone is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}


		if(!this.password){
		toast.error("Password is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}


				const res = await axios.post(this.$store.state.api_url+'register',{
					email : this.email,
					phone : this.phone,
					password : this.password
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				if(res=='success'){
					this.show_modal = true
					this.send_otp()
				}else{
				toast.error("An account with that email already exists", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });	
				}

			}


		}


	}
</script>