<template>



<section style="padding-top: 50px;">
<div class="container">

<div class="row">
	<div class="col-md-2"></div>
<div class="col-md-4" style="padding-top: 30px;">
	<img src="/assets/images/3156814-removebg-preview.png" style="width: 100%;">
</div>
<div class="col-md-4">

<div style="padding-top:20px;">
<h3 class="logo">Coil Chat</h3>
</div>



<div style="background-color: none; padding:10px;">

	<div>
<h3>Forgot password</h3>
</div>
	<form @submit.prevent="send_link()">
		<div class="form-floating mb-3">
  <input type="text" class="form-control" id="floatingInput" placeholder="Email address" v-model="email">
  <label for="floatingInput">Email</label>
</div>

<div style="padding-top:10px;">
	<button class="btn btn-2 btn-lg" type="submit">Submit</button>
	<router-link to="/" class="btn text-danger btn-lg">Back to login</router-link>
</div>
	</form>

</div>




<div style="color: grey;">
	<p>Coil Chat Inc.</p>
</div>

</div>
<div class="col-md-2"></div>
</div>

</div>
</section>





</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return{
				email : '',
			
			}
		},
		methods : {


			async send_link(){

				if(!this.email){
					toast.error("Email is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
				}

				const res = await axios.post(this.$store.state.api_url+'send-password-link',{
					email : this.email
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				
				if(res=='success'){
					toast.error("A new password reset link ha been sent to your email", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
				}else{
					toast.error("Email is does not exist", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
				}

				this.email = ''
				
			}

		}


	}
</script>