<template>



<section style="padding-top: 50px;">
<div class="container">

<div class="row">
	<div class="col-md-2"></div>
<div class="col-md-4" style="padding-top: 30px;">
	<img src="/assets/images/3156814-removebg-preview.png" style="width: 100%;">
</div>
<div class="col-md-4">

<div style="padding-top:20px;">
<h3 class="logo">Coil Chat</h3>
</div>



<div style="background-color: none; padding:10px;">

	<div>
<h3>Login</h3>
</div>
	<form @submit.prevent="login()">
		<div class="form-floating mb-3">
  <input type="text" class="form-control" id="floatingInput" placeholder="Email address" v-model="email">
  <label for="floatingInput">Email</label>
</div>
<div class="form-floating">
  <input type="password" class="form-control" id="floatingPassword" placeholder="Password" v-model="password">
  <label for="floatingPassword">Password</label>
</div>

<div style="padding-top:10px;">
	<button class="btn btn-2 btn-lg" type="submit">Login</button>
	<router-link to="/forgot-password" class="btn text-danger btn-lg">Forgot password</router-link>
</div>
	</form>

</div>

<router-link to="/register" class="btn text-dark btn-lg">Don't have an account?</router-link>


<div style="color: grey;">
	<p>Coil Chat Inc.</p>
</div>

</div>
<div class="col-md-2"></div>
</div>

</div>
</section>



<!-- otp modal -->
<div style="position: fixed; width: 100%; top: 0; padding-top: 200px; z-index: 900;" v-if="show_modal">
	
	<div class="container">
		<div class="row">
			<div class="col-md-4"></div>
			<div class="col-md-4">
				<div class="shadow-lg rounded p-3" style="background-color:#fff;">

					<div class="row">
						<div class="col-md-9 col-9">
								<p>Enter OTP send to your email</p>
						</div>
						<div class="col-md-3 col-3">
							<div style="text-align:right;">
						<button class="btn text-danger" @click="show_modal=false"> <i class="bi bi-x"></i> </button>
					</div>
						</div>
					</div>

					
					
					<input type="text" v-model="otp" class="form-control" placeholder="OTP">
					<button class="btn btn-2 mt-3" @click="confirm_otp()">Send</button>
				</div>
			</div>
			<div class="col-md-4"></div>
		</div>
	</div>

</div>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return{
				email : '',
				password : '',
				details : [],
				show_modal : false,
				otp : ''
			}
		},
		methods : {

				async confirm_otp(){

				if(!this.otp){
		toast.error("OTP is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}
		
				const res = await axios.post(this.$store.state.api_url+'confirm-otp',{
					email : this.email,
					otp : this.otp
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				if(res=='success'){
					
					toast.success("Account verified,you can now login", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
			this.show_modal = false


				}else{

					toast.error("Invalid OTP!", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });

				}
				
			},


			async send_otp(){
			this.show_modal = true
				const res = await axios.post(this.$store.state.api_url+'send-otp',{
					email : this.email
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				
			},


			async login(){
				if(!this.email){
		toast.error("Email is required", {
        autoClose: 1000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}


		if(!this.password){
		toast.error("Password is required", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      });
      return

		}


				const res = await axios.post(this.$store.state.api_url+'login',{
					email : this.email,
					password : this.password
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				this.details = res

				if(res=='login fail'){
					toast.error("Wrong email or password!", {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      });
				
				}else if(res=='email not verified'){
					toast.error('Account not verified', {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      });
					

			this.send_otp()
				
				}else if(res.length>0){
					res.forEach((data)=>{
						this.$store.state.username = data.userName
						localStorage.setItem('username',data.userName)

						this.$store.state.logged_in = true
						localStorage.setItem('logged_id',true)

						this.$store.state.user_id = data.userId 
						localStorage.setItem('user_id',data.userId)

						this.$store.state.user_hash = data.userHash
						localStorage.setItem('user_hash',data.userHash)

						localStorage.setItem('email',data.email)
						localStorage.setItem('phone',data.phone)
						this.$store.state.email = localStorage.getItem('email')
						this.$store.state.phone = localStorage.getItem('phone')

						localStorage.setItem('total_friends',data.total_friends)
						this.$store.state.total_friends = localStorage.getItem('total_friends')



						this.$router.push('/home')

					})


				}

				
				
			}
		},
		created(){
		
		toast.success("Welcome to Coil Chat", {
        autoClose: 3000,
        theme : 'dark',
        position:'bottom-right'
      });

		}
	}
</script>