<template>

<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
<div class="row">
	<div class="col-md-10">
		<h5> <router-link to="/post-comment-notifications" class="btn"> <i class="bi bi-chevron-left icon"></i> Back </router-link></h5>
	</div>
	
</div>
</div>
<section style="padding-top:0px;">

	<h5 style="color: grey;"> {{post_title}} </h5>

<div class="row">
	<div class="col-md-8 col-8">
	<p>Someone commented:</p>
</div>
<div class="col-md-4 col-4" style="text-align:right;">
	<button class="btn btn-2 btn-sm" @click="delete_friend()"> <i class="bi bi-x-circle"></i> Remove friend</button>
</div>
</div>

<div>

<div v-for="detail in details">
	<label class="post-comment"> {{detail.message}} </label>
</div>	

</div>


<div style="padding-top:20px;">
	<!-- replies -->


<div v-for="reply in replies">
	
	<div class="response">

	<div class="row">
		<div class="col-md-1 col-1"> <h3><i class="bi bi-check-circle icon"></i></h3> </div>
		<div class="col-md-10 col-10"> {{reply.message}}    </div>
		<div class="col-md-1 col-1" style="text-align:right;"> <button class="btn text-danger" @click="delete_reply(reply.id)"> <i class="bi bi-x-circle"></i> </button> </div>
	</div>

	</div>

</div>

</div>


<div>


<form @submit.prevent="send_reply()">
	
<div class="input-group mb-3 mt-3">
  <input v-model="message" type="text" class="form-control input-design" placeholder="Reply...">
  <button class="btn btn-1" type="submit"><i class="bi bi-arrow-right-circle"></i> </button>
</div>

</form>	


</div>



</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				socket : io(this.$store.state.socketio_server),
				details : [],
				comment_id : '',
				post_id : '',
				post_data : [],
				post_title : '',
				message : '',
				comment_owner : '',
				replies : []
			}
		},
		methods : {

			async delete_friend(){
				const res = await axios.post(this.$store.state.api_url+'delete-friend',{
					user_id : this.$store.state.user_id,
					friend : this.comment_owner
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				toast.success("Friend removed from your klub", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })

	this.$router.push('/post-comment-notifications')
				
			},


				async send_reply(){
			if(!this.message){
				return
					}

  		 this.socket.emit('send reply',{ 'user_id' : this.$store.state.user_id,'message' : this.message,'post_id':this.post_id,'comment_id':this.comment_id, 'comment_owner' : this.comment_owner })
  			this.message = ''
  			this.comment_replies()
		},


		

		
		async comment_details(){
				const res = await axios.get(this.$store.state.api_url+'comment-details/'+this.comment_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.details = res
				res.forEach((data)=>{
					this.post_id = data.post_id
					this.comment_owner = data.user_id 
				})
				if(this.post_id){
					this.post_details()
				}

			},

			async post_details(){
				const res = await axios.get(this.$store.state.api_url+'post-details/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.post_data = res
				res.forEach((data)=>{
					this.post_title = data.postTitle
				})
			},

			async comment_replies(){
			const res = await axios.get(this.$store.state.api_url+'comment-replies/'+this.comment_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.replies = res

		},

		async delete_reply(id){
				const res = await axios.post(this.$store.state.api_url+'delete-reply',{
					id : id 
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				this.comment_replies()
			
			},
		
		},

		created(){
		this.comment_id = this.$route.params.id 
		this.comment_details()
		this.comment_replies()


		//begining of socket.io

		this.socket.on("connect", () => {

		//check if the reply was received 
		this.socket.on('reply received',(data)=>{
			//comment received 
		})


		})



		
		},


	}


</script>