<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div style="padding-top:30px;">
	<form @submit.prevent="edit_account()">
		<div class="row">
			<div class="col-md-6 col-6">
			<div class="form-floating mb-3">
  <input disabled type="email" class="form-control" v-model="email" placeholder="Email">
  <label for="floatingInput">Email</label>
</div>	
			</div>

			<div class="col-md-6 col-6">
			<div class="form-floating mb-3">
  <input type="text" class="form-control" v-model="phone" placeholder="Phone">
  <label for="floatingInput">Phone</label>
</div>	
			</div>

<div class="col-md-6 col-12">
<div class="form-floating">
  <input type="password" class="form-control" placeholder="Password"  v-model="password">
  <label for="floatingPassword">Password</label>
</div>	
</div>

</div>

<div style="padding-top:5px;">
	<button class="btn btn-1 btn-lg" type="submit">Save changes</button>
</div>
	</form>
</div>


<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import { toast } from 'vue3-toastify'
	import 'vue3-toastify/dist/index.css'
	import axios from 'axios'
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return {
				email : '',
				phone : '',
				password : ''
			}
		},
		methods : {

			async edit_account(){
				const res = await axios.post(this.$store.state.api_url+'edit-account',{
					user_id : this.$store.state.user_id,
					email : this.email,
					phone : this.phone,
					password : this.password
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
			toast.success("Changes saved", {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      })

		//update on the browser too
		localStorage.setItem('email',this.email)
		localStorage.setItem('phone',this.phone)

			}
			
		},

		created(){
			this.email = localStorage.getItem('email')
			this.phone = localStorage.getItem('phone')
		}

	}
</script>