<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div>
	<small style="font-size: 16px;">Create new post</small>
</div>

<!-- Whats on your mind -->
<form @submit.prevent="new_post()">
<div class="form-floating">
<textarea @input="get_words()" class="form-control" placeholder="Leave a comment here" id="floatingTextarea2" style="height: 100px" v-model="post_title"></textarea>
<label for="floatingTextarea2">What's on your mind?</label>
</div>

<div style="padding-top:2px;">
<div class="progress" style="height: 3px;">
  <div class="progress-bar bg-warning" role="progressbar" :style=" { width :  (characters.length/character_limit)*100+'%' } " :aria-valuenow="characters.length" aria-valuemin="0" :aria-valuemax="100"></div>
</div>
</div>

<p v-if="detected_bad_words.length>0" class="text-danger"> Bad words detected! :  {{detected_bad_words}} </p>

<div style="padding-top:5px;" class="row">
	<div class="col-md-6 col-6">
		<button type="submit" class="btn btn-1" :disabled="characters.length>=character_limit || detected_bad_words.length>0"> <i class="bi bi-plus"></i>Create post</button>
	</div>
	
</div>
</form>



<!-- Trending topics -->
<section>
<div class="container">

<div class="row">
<div class="col-md-0"></div>
<div class="col-md-12" style="padding-top: 20px; text-align: center;">


<div>
	<router-link v-for="post in posts" class="btn my-post shadow" :to="{ 'name' : 'post-details', params : { 'post_id' : post.post_id } }">
		{{post.post_title}}
	</router-link>

</div>

<p v-if="posts.length==0" class="text-danger"> <i class="bi bi-info-circle"></i> No posts available</p>


</div>
<div class="col-md-0"></div>
</div>

</div>
</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';
	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return{
				post_title : '',
				posts : [],
				post_id : '',
				comments : [],
				likes : [],
				bad_words : ['fuck','kill','bullshit'],
				detected_bad_words : [],
				characters : '',
				character_limit : 300
			}
		},

		methods : {

			get_words(){
				this.characters = this.post_title

				this.detected_bad_words = []
				this.bad_words.forEach((data)=>{
					if(this.post_title.search(data)>=0){
						this.detected_bad_words.push(data)
					}
				})

			},

			async new_post(){
				const res = await axios.post(this.$store.state.api_url+'new-post',{
					post_title : this.post_title,
					user_id : this.$store.state.user_id
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
			
				
		toast.success("Post created successfully", {
        autoClose: 3000,
        theme : 'dark',
        position:'top-center'
      });
		this.my_posts()
		this.post_title = ''
			},

		async my_posts(){
			const res = await axios.get(this.$store.state.api_url
				+'my-posts/'+this.$store.state.user_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.posts = res
		},

		async delete_post(post_id){
			const res = await axios.post(this.$store.state.api_url+'delete-post',{
				post_id : post_id
			}).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.my_posts()
			
		},

		async like_post(post_id){
			const res = await axios.post(this.$store.state.api_url+'like-post',{
				post_id : post_id,
				user_id : this.$store.state.user_id
			}).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})

			this.my_posts()
			
		},

		comment_details(post_id){
			this.post_id = post_id
			this.post_comments()
			this.comment_likes()
		},

		async post_comments(){
			const res = await axios.get(this.$store.state.api_url
				+'post-comments/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.comments = res
		},

		async comment_likes(comment_id){
			const res = await axios.get(this.$store.state.api_url+'comment-likes/'+this.post_id+'/'+comment_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.likes = res

		}


		},

		created(){
			this.my_posts()
		}
	}
</script>