<template>
	<div>
	<button style="text-align:left;" @click="create_topic(topic.topicName)" class="btn response shadow-sm" v-for="topic in topics">{{topic.topicName}}</button>
</div>
</template>



<script>
import axios from 'axios'

export default{
	name : 'trending-topics',
	data(){
		return {
			topics : []
		}
	},
	props : {
		create_chat_handler :{type: Function},
		topic : {type: String}
	},

	methods : {

		create_topic(topic_1){
			this.$emit('receive-topic',topic_1)
			this.create_chat_handler()
		},

		async trending_topics(){
			const res = await axios.get(this.$store.state.api_url+'trending-topics').then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.topics = res
		}
	},
	created(){
		this.trending_topics()
	}

}

</script>