<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />


<div>
	
	<div style="text-align:center; padding-top:50px;">
	<h5> {{this.$store.state.chat_topic}} </h5>
	</div>

	<div style="text-align:center;">
		<h6>Waiting someone to join chat</h6>
		   <div class="d-flex justify-content-center text-warning">
  <div class="spinner-border" role="status">
    <span class="visually-hidden">Loading...</span>
  </div>
</div>

	</div>


	<div style="text-align:center; padding-top:20px;">
		<button class="btn btn-2" @click="leave_waiting_room()">Leave room</button>
	</div>

	<div style="text-align:center; color:grey;">
		<small> {{minutes}} : {{seconds}}</small>
	</div>

</div>




<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>



</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import { io } from "socket.io-client"

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data(){
			return {
				minutes : 3,
				seconds : '00',
				distance_period : (1*60*1000),
				socket : io(this.$store.state.socketio_server),
				polling : ''
			}
		},
		methods : {
			timer(){
  			this.polling = setInterval(()=>{
  			this.minutes = Math.floor(this.distance_period % (1000 * 60 * 60) / (1000 * 60) ) 
  			this.seconds = Math.floor(this.distance_period % (1000 * 60) / 1000 ) 
  			this.distance_period = this.distance_period-1000
  			if(this.distance_period<1){
  				this.distance_period = (1*60*1000)
  				clearInterval(this.polling)
  				this.$router.push('/home')
  			}
  			
  			},1000)
  			return
			},

			leave_waiting_room(){

				this.socket.emit('leave klub waiting room',{'room' : this.$store.state.current_topic_hash })
				clearInterval(this.polling)
				this.$router.push('/my-klub')

			}

		},

		mounted(){
				this.timer()

				this.socket.on('connect',()=>{
					this.socket.send('user in the waiting klub room')
					this.socket.emit('joined klub waiting room',{'klub_waiting_room_id': this.$store.state.current_topic_hash })
					this.socket.emit('click klub topic',{'room': 'klub topics page', 'clicked_topic_hash' : '' })
				})


				//add this topic to list of topics to be chosen
				this.socket.emit('fetch klub chat topics',{'room': 'klub topics page' })

				//check if someone clicked this topic
				this.socket.on('my klub clicked topic',(data)=>{
					if(data==this.$store.state.current_topic_hash){
						clearInterval(this.polling)
						this.$router.push('/klub-chat-room')
					}
				})

			}
	}
</script>