<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<div class="post-title-section rounded shadow">
<div class="row">
	<div class="col-md-8 col-8">
		<h5>{{post_title}}</h5>
	</div>
	<div class="col-md-4 col-4" style="text-align:right;">

		<span v-if="minutes_duration>59">{{hours_duration}}hrs ago</span>
		<span v-if="minutes_duration<=59">{{minutes_duration}}m ago</span>

	<button class="btn" @click="like_post()"> <i class="bi bi-heart-fill icon" v-if="this.post_was_liked"></i> <i class="bi bi-heart icon" v-if="!this.post_was_liked"></i> </button>	
	</div>
</div>
</div>
<section style="padding-top:0px;">
<div id="box" style="height: 300px; overflow: scroll; padding-top: 20px; padding-bottom: 0px;">
		<div v-for="message in messages" style="margin-bottom: 0px;" v-if="messages.length>0">
	<label class="post-comment">
		{{message.message}}
	</label>
	<div>
		<button class="btn" @click="comment_details(message.id,message.message)"> <i class="bi bi-chat icon"></i> View replies </button>



		
	</div>

	</div>
	<p style="font-size:20px;" v-if="messages.length==0">No comments yet</p>
</div>


	<div style="padding-top:30px;">
<form @submit.prevent="post_comment()">
	<div class="input-group mb-3">
  <input v-model="message" type="text" class="form-control input-design" placeholder="Leave a comment...">
  <button class="btn btn-1" type="submit"><i class="bi bi-arrow-right-circle"></i> </button>
</div>
</form>
	</div>



<div class="reply-box shadow-lg rounded" v-if="show_reply_box" id="replies-box" style="min-width: 200px;">

	<div class="row">
		<div class="col-md-10 col-10">
			<h6 style="color:grey;">{{selected_comment}}</h6>
			<small> {{replies.length}} replie(s) </small>
		</div>
		<div class="col-md-2 col-2" style="text-align:right;">
			<button class="btn p-0" @click="show_reply_box=false"> <i class="bi bi-x icon"></i> </button>
		</div>
	</div>
	
	<div class="replies-box" v-if="replies.length>0">
		<div v-for="reply in replies" style="margin-top: 5px;">
	<label class="replies"> {{reply.message}} </label>
	</div>
	</div>

	<label v-if="replies.length==0"> No replies yet </label>

	
	</div>



</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				details : [],
				post_id : '',
				post_title : '',
				message : '',
				socket : io(this.$store.state.socketio_server),
				messages : [],
				show_reply_box : false,
				selected_comment_id : '',
				selected_comment : '',
				reply : '',
				replies : [],
				likes : [],
				post_owner : '',
				expires_on : '',
				hours_duration : '',
				minutes_duration: '',
				created : '',
				post_was_liked : false
			}
		},
		methods : {

			post_duration(){
				let today = new Date()
				let date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate()
				let time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
				let now = date+' '+time;

				this.created = new Date(this.created).getTime()
				let diff = new Date(now).getTime() - this.created
				let hours = Math.round(diff/(1000*3600))
				let minutes = Math.round((diff/(1000*3600))*60)

				this.hours_duration = hours
				this.minutes_duration = minutes
				

			},

			like_post(){
				this.socket.emit('like post',{ 'user_id' : this.$store.state.user_id, 'post_id' : this.post_id, 'post_owner' : this.post_owner })
				this.check_post_like()
			},

	

			async post_details(){
			const res = await axios.get(this.$store.state.api_url
				+'post-details/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.details = res
				res.forEach((data)=>{
					this.post_title = data.postTitle 
					this.post_owner = data.userId
					this.expires_on = data.expiresOn 
					this.created = data.created 
				})
				this.post_comments()
				this.post_duration()
		},

		async post_comments(){
			const res = await axios.get(this.$store.state.api_url
				+'post-comments/'+this.post_id).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})
				this.messages = res
		},

		async post_comment(){
  		 await this.socket.emit('post msg',{ 'user_id' : this.$store.state.user_id,'message' : this.message,'room':this.post_id, 'post_owner' : this.post_owner })

  			this.message = ''
  			this.post_comments()
		},

		async send_reply(){
  		 await this.socket.emit('send reply',{ 'user_id' : this.$store.state.user_id,'message' : this.reply,'post_id':this.post_id,'comment_id':this.selected_comment_id})
  			this.reply = ''
  			this.comment_replies()
		},

		async scroll_top(){
  		var container = this.$el.querySelector("#box");
		container.scrollTop = container.scrollHeight;
		},

		

		async like_comment(comment_id){
	await this.socket.emit('like comment',{ 'user_id' : this.$store.state.user_id,'post_id' : this.post_id,'comment_id':comment_id})
		},

		comment_details(comment_id,message){
			this.selected_comment_id = comment_id
			this.selected_comment = message
			this.show_reply_box = true
			this.comment_replies()
		},

		async comment_replies(){
			const res = await axios.get(this.$store.state.api_url+'comment-replies/'+this.selected_comment_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.replies = res

		},

			async my_comment_likes(){
			const res = await axios.get(this.$store.state.api_url+'my-comment-likes/'+this.post_id+'/'+this.$store.state.user_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			this.likes = res

		},

		async check_post_like(){
			const res = await axios.get(this.$store.state.api_url+'check-post-like/'+this.post_id+'/'+this.$store.state.user_id).then(function(response){
				return response.data
			}).catch(function(error){
				console.log(error)
			})
			
			if(res.length>0){
				this.post_was_liked = true
			}else{
				this.post_was_liked = false
			}

		}
		
		},
		
		created(){
		this.post_id = this.$route.params.post_id	
		this.post_details()
		this.my_comment_likes()
		this.check_post_like()

		

		//check connection and join room
		this.socket.on("connect", () => {
  		this.socket.send('user connected')
  		console.log('connected')
  		//join this room
		this.socket.emit('joinroom',{'post_id':this.post_id})
		
			})

		//check if someone joined post
		this.socket.on("roomjoined", (result) => {
			console.log(result)
			})

		//check if there is new message from the server
		this.socket.on("new post comment", (result) => {
			//this.messages = this.messages.concat(result)
			console.log(result)
			this.post_comments()
			this.scroll_top()
			return
			})

		//check if comment was liked
		this.socket.on("comment liked", (result) => {
			console.log(result)
			this.post_comments()
			this.my_comment_likes()
			})
		//check if reply was received
		this.socket.on('reply received',(result)=>{
			//alert(result)
		})

		//check disconnections
		this.socket.on("someone left", () => {
			toast.error("Someone left", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-right'
      });

			})


		
		},

		updated(){
			this.scroll_top()
		}
	}


</script>