<template>



<section>
<div class="container">

<div class="row">
<div class="col-md-3"></div>
<div class="col-md-6" style="background-color: #F7F7F7; border-right: solid 1px #F1F1F1; border-left: solid 1px #F1F1F1;">

<TopBar />

<Menu />

<section style="padding-top:0px;">
<div style="text-align:center; padding-top:50px;">
<h1> <i class="bi bi-chat icon"></i> </h1>
<h6>Chatting time is over</h6>
<h6>Do you want to add member to your klub?</h6>	

<div>
	<button class="btn btn-2" style="margin-right: 5px;" @click="add_friend()">Yes</button>
	<router-link to="/home" class="btn btn-1">No</router-link>
</div>
	
</div>
	

</section>



<BottomMenu />


<Footer />





</div>
<div class="col-md-3"></div>
</div>

</div>
</section>


</template>

<script>
	import Footer from './Footer'
	import TopBar from './TopBar'
	import Menu from './Menu'
	import BottomMenu from './BottomMenu'
	import axios from 'axios'
	import { io } from "socket.io-client"

	import { toast } from 'vue3-toastify';
	import 'vue3-toastify/dist/index.css';

	export default{
		name : 'home',
		components : {Footer,TopBar,Menu,BottomMenu},
		data (){
			return {
				socket : io(this.$store.state.socketio_server),
				messages : [],
				message : '',
				minutes : 3,
				seconds : '00',
				distance_period : (1*60*1000),
				polling : ''
			}
		},
		methods : {

			async add_friend(){
				const res = await axios.post(this.$store.state.api_url+'add-friend',{
					user_id : this.$store.state.user_id,
					friend : this.$store.state.friend
				}).then(function(response){
					return response.data
				}).catch(function(error){
					console.log(error)
				})

				toast.success("Request sent!", {
        autoClose: 2000,
        theme : 'dark',
        position:'top-center'
      })


				setTimeout(()=>{
				this.$router.push('/home')	
				},2000);

				
			}

		},
		
		
	}


</script>